.component-container{
    width:calc(100% - 10px);
    overflow: auto;
}

.chips-container{
    padding-left:0px;
    width: fit-content;
    white-space:nowrap;
}

div.the-chip{
    background-color:white;
    color:black;
    border:solid 0.5px;
    margin-left:5px;
}

span.chip-value {
    font-size: 10px;
    font-weight: bold;
    color: black;
    border: solid 1px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 26px;
    height: 23px;
}

::-webkit-scrollbar{ 
    margin-top: 10px;
    height:7px;
}

::-webkit-scrollbar-thumb{
    height:5px;
    border-radius: 999px;
    background-color:black;
    /* border-bottom-right-radius:0; */
}